<template>
  <div id="edit">
    <que-header></que-header>
    <div id="loading" v-loading="loading">
      <div id="editpage" v-show="onload">
        <div id="queType">
          <!--左侧栏-->
          <div id="title">
            <img
              src="../assets/pictures/addQue.svg"
              width="18px"
              height="18px"
            />
            <div>添加题目</div>
          </div>
          <div id="types">
            <div v-for="(item, index) in types" :key="index" class="types">
              <li @click="conduct('cQ', item)">{{ item }}</li>
            </div>
          </div>
        </div>

        <div id="questionnaire">
          <!--问卷编辑区-->
          <div id="queBox">
            <div id="head">
              <div id="name">
                <!--问卷名称-->
                <input
                  type="text"
                  v-model="queName"
                  autocomplete="off"
                  v-if="isEdName"
                  @focus="queName = queName == '问卷名称' ? '' : queName"
                  @blur="quitEdName"
                  @keyup.enter="quitEdName"
                  v-focus
                />
                <span @click="isEdName = true" v-else>{{ queName }}</span>
              </div>
              <div id="interpretation">
                <!--问卷说明-->
                <input
                  type="text"
                  v-model="queIntp"
                  autocomplete="off"
                  v-if="isEdIntp"
                  @focus="queIntp = queIntp == '问卷说明' ? '' : queIntp"
                  @blur="quitEdIntp"
                  @keyup.enter="quitEdIntp"
                  v-focus
                />
                <span @click="isEdIntp = true" v-else>
                  {{ queIntp }}
                </span>
              </div>
            </div>

            <div class="tip" v-if="showTip">
              <span style="color: #00A0E9;font-size: 18px;">☁</span>
              待添加题目~
            </div>

            <div id="models" ref="models">
              <draggable
                v-model="ques"
                @update="itemSwitched"
                handle=".generalMover"
                :scroll="true"
              >
                <transition-group name="list-complete" tag="div">
                  <div
                    v-for="(que, index) in ques"
                    :key="que.id"
                    :class="[
                      'list-complete-item',
                      que.showEdArea ? 'activeBG' : '',
                    ]"
                    @click="
                      (e) => {
                        que.type == 'pagingLine'
                          ? (que.showEdArea = !que.showEdArea)
                          : editQue(que, e);
                      }
                    "
                  >
                    <img
                      src="../assets/pictures/drag.svg"
                      width="20px"
                      :class="[
                        'generalMover',
                        que.type === 'pagingLine' ? 'mover-left' : '',
                      ]"
                    />
                    <div
                      class="que-title-holder"
                      v-if="que.type != 'pagingLine'"
                    >
                      <div class="redStarHolder">
                        <img
                          v-if="que.compulsory"
                          src="../assets/pictures/redStar.svg"
                          alt="*"
                          width="7px"
                          height="7px"
                        />
                        <div v-else style="width: 7px;height: 7px;"></div>
                      </div>
                      <label class="queIndex" v-if="que.showEdArea"
                        >{{ queUltimateIndex(que.id) + 1 }}.</label
                      >
                      <input
                        type="text"
                        v-model="que.title"
                        v-if="que.showEdArea"
                        autocomplete="off"
                      />
                      <span v-else
                        >{{ queUltimateIndex(que.id) + 1 }}.
                        {{ que.title }}</span
                      >
                      <div class="pic-holder" v-if="que.showEdArea">
                        <input
                          type="file"
                          name="file"
                          class="file canNotSee"
                          :id="que.id"
                          @change="(event) => picHandler(event, que, 'title')"
                        />
                        <label :for="que.id" class="label-file label-item"
                          ><img
                            v-if="!que.titleImgSrc"
                            src="../assets/pictures/pic.svg"
                            width="18px"
                          />
                          <img v-else :src="que.titleImgSrc" class="pic-item" />
                        </label>
                        <div v-if="que.titleImgSrc != ''" class="deletePic">
                          <i
                            class="el-icon-close"
                            @click="delTitleImg(que)"
                          ></i>
                        </div>
                      </div>
                    </div>
                    <div class="pic-preview">
                      <el-image
                        v-if="que.titleImgSrc && !que.showEdArea"
                        :src="que.titleImgSrc"
                        :preview-src-list="
                          que.showEdArea ? [] : [que.titleImgSrc]
                        "
                        fit="cover"
                        style="width: 135px;height: 90px;border-radius: 5px;"
                      ></el-image>
                    </div>

                    <div v-if="que.type == '单选题'">
                      <!--单选模板-->
                      <draggable
                        v-model="que.extension.items"
                        handle=".mover"
                        :disabled="!que.showEdArea"
                        :scroll="true"
                      >
                        <transition-group name="choices" tag="div">
                          <div
                            v-for="(choice, index) in que.extension.items"
                            :key="choice.id"
                            class="choices-item"
                          >
                            <li>
                              <img
                                src="../assets/pictures/drag.svg"
                                width="20px"
                                class="mover"
                                v-if="que.showEdArea"
                              />
                              <div>
                                <input
                                  type="radio"
                                  v-model="que.extension.value"
                                  :value="choice.content"
                                  class="choiceBox"
                                  disabled
                                />
                              </div>
                              <input
                                type="text"
                                v-model="choice.content"
                                autocomplete="off"
                                class="choicesInput"
                                v-if="que.showEdArea"
                              />
                              <span v-else>
                                {{ choice.content }}
                              </span>
                              <div class="choiceIcon" v-if="que.showEdArea">
                                <div class="pic-holder">
                                  <input
                                    type="file"
                                    name="file"
                                    class="file canNotSee"
                                    :id="choice.fileId"
                                    @change="
                                      (event) =>
                                        picHandler(event, choice, 'item')
                                    "
                                  />
                                  <label
                                    :for="choice.fileId"
                                    class="label-file label-item"
                                  >
                                    <img
                                      v-if="!choice.imgSrc"
                                      src="../assets/pictures/pic.svg"
                                      width="16px"
                                    />
                                    <img
                                      v-else
                                      :src="choice.imgSrc"
                                      class="pic-item"
                                    />
                                  </label>
                                  <div class="deletePic" v-if="choice.imgSrc">
                                    <i
                                      class="el-icon-close"
                                      @click="delItemImg(choice)"
                                    ></i>
                                  </div>
                                </div>
                                <img
                                  src="../assets/pictures/delete.svg"
                                  width="16px"
                                  @click="
                                    deleteCS(que.extension.items, index, que.id)
                                  "
                                />
                              </div>
                            </li>
                            <div class="pic-preview">
                              <el-image
                                v-if="!que.showEdArea && choice.imgSrc != ''"
                                :src="choice.imgSrc"
                                :preview-src-list="[choice.imgSrc]"
                                fit="cover"
                                style="width: 135px;height: 90px;border-radius: 5px;"
                              ></el-image>
                            </div>
                          </div>
                        </transition-group>
                      </draggable>
                      <div
                        class="edArea"
                        v-if="que.type == '单选题' && que.showEdArea"
                      >
                        <div class="add" @click="addCS(que.extension)">
                          <img src="../assets/pictures/add.svg" width="18px" />
                          <div>添加选项</div>
                        </div>
                        <div class="edhead">题目设置</div>
                        <div class="item">
                          是否必填<input
                            type="checkbox"
                            v-model="que.compulsory"
                            class="checkBox"
                          />
                        </div>
                        <div class="item">
                          随机选项<input
                            type="checkbox"
                            v-model="que.random"
                            class="checkBox"
                          />
                        </div>
                        <div class="item jump">
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.extension.items, 'bfa')"
                              >选项后填空</el-button
                            >
                          </div>
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.showUpCondition, que.id)"
                              >显示条件</el-button
                            >
                          </div>
                        </div>
                      </div>
                    </div>

                    <div v-else-if="que.type == '多选题'">
                      <!--多选模板-->
                      <draggable
                        v-model="que.extension.items"
                        handle=".mover"
                        :disabled="!que.showEdArea"
                        :scroll="true"
                      >
                        <transition-group name="options" tag="div">
                          <div
                            v-for="(option, index) in que.extension.items"
                            :key="option.id"
                            class="options-item"
                          >
                            <li>
                              <img
                                src="../assets/pictures/drag.svg"
                                width="20px"
                                class="mover"
                                v-if="que.showEdArea"
                              />
                              <div>
                                <input
                                  type="checkbox"
                                  :value="option.content"
                                  v-model="que.extension.value"
                                  class="choiceBox"
                                  disabled
                                />
                              </div>
                              <input
                                type="text"
                                v-model="option.content"
                                autocomplete="off"
                                class="choicesInput"
                                v-if="que.showEdArea"
                              />
                              <span v-else>{{ option.content }}</span>
                              <div class="choiceIcon" v-if="que.showEdArea">
                                <div class="pic-holder">
                                  <input
                                    type="file"
                                    name="file"
                                    class="file canNotSee"
                                    :id="option.fileId"
                                    @change="
                                      (event) =>
                                        picHandler(event, option, 'item')
                                    "
                                  />
                                  <label
                                    :for="option.fileId"
                                    class="label-file label-item"
                                    ><img
                                      v-if="!option.imgSrc"
                                      src="../assets/pictures/pic.svg"
                                      width="16px"/>
                                    <img
                                      v-else
                                      :src="option.imgSrc"
                                      class="pic-item"
                                  /></label>
                                  <div class="deletePic" v-if="option.imgSrc">
                                    <i
                                      class="el-icon-close"
                                      @click="delItemImg(option)"
                                    ></i>
                                  </div>
                                </div>
                                <img
                                  src="../assets/pictures/delete.svg"
                                  width="16px"
                                  @click="deleteOM(que.extension.items, index)"
                                />
                              </div>
                            </li>
                            <div class="pic-preview">
                              <el-image
                                v-if="!que.showEdArea && option.imgSrc != ''"
                                :src="option.imgSrc"
                                :preview-src-list="[option.imgSrc]"
                                fit="cover"
                                style="width: 135px;height: 90px;border-radius: 5px;"
                              ></el-image>
                            </div>
                          </div>
                        </transition-group>
                      </draggable>

                      <div
                        class="edArea"
                        v-if="que.type == '多选题' && que.showEdArea"
                      >
                        <div class="add" @click="addOM(que.extension)">
                          <img src="../assets/pictures/add.svg" width="18px" />
                          <div>添加选项</div>
                        </div>
                        <div class="edhead">题目设置</div>
                        <div class="item">
                          是否必填<input
                            type="checkbox"
                            v-model="que.compulsory"
                            class="checkBox"
                          />
                        </div>
                        <div class="item">
                          随机选项<input
                            type="checkbox"
                            v-model="que.random"
                            class="checkBox"
                          />
                        </div>
                        <div class="item">
                          勾选上下限
                          <div class="max-min">
                            最多<input
                              type="text"
                              v-model="que.extension.max"
                              @blur="multiMaxCheck(que.extension, false)"
                              @keyup.enter="multiMaxCheck(que.extension, false)"
                            />项
                          </div>
                          <div class="max-min">
                            最少<input
                              type="text"
                              v-model="que.extension.min"
                              @blur="multiMinCheck(que.extension, false)"
                              @keyup.enter="multiMinCheck(que.extension, false)"
                            />项
                          </div>
                        </div>
                        <div class="item jump">
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.extension.items, 'bfa')"
                              >选项后填空</el-button
                            >
                          </div>
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.showUpCondition, que.id)"
                              >显示条件</el-button
                            >
                          </div>
                        </div>
                      </div>
                    </div>

                    <div v-else-if="que.type == '填空题'">
                      <!--填空模板-->
                      <div>
                        <textarea
                          type="text"
                          v-model="que.extension"
                          class="blank"
                          placeholder="文本框"
                          disabled
                        ></textarea>
                      </div>
                      <div
                        class="edArea"
                        v-if="que.type == '填空题' && que.showEdArea"
                      >
                        <div class="edhead">题目设置</div>
                        <div class="item">
                          是否必填<input
                            type="checkbox"
                            v-model="que.compulsory"
                            class="checkBox"
                          />
                        </div>
                        <div class="item jump">
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.showUpCondition, que.id)"
                              >显示条件</el-button
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                    <div v-else-if="que.type == '排序题'">
                      <draggable
                        v-model="que.extension.items"
                        handle=".mover"
                        :disabled="!que.showEdArea"
                        :scroll="true"
                      >
                        <transition-group name="sortItems" tag="div">
                          <div
                            v-for="(sortItem, index) in que.extension.items"
                            :key="sortItem.id"
                            class="sortItems-item"
                          >
                            <li>
                              <img
                                src="../assets/pictures/drag.svg"
                                width="20px"
                                class="mover"
                                v-if="que.showEdArea"
                              />
                              <div>
                                <div
                                  class="orderBox"
                                  @click.stop="
                                    () => {
                                      return;
                                    }
                                  "
                                >
                                  <!-- {{ sortItem.order ? sortItem.order : "" }} -->
                                </div>
                              </div>
                              <input
                                type="text"
                                v-model="sortItem.content"
                                autocomplete="off"
                                class="choicesInput"
                                v-if="que.showEdArea"
                              />
                              <span v-else>
                                {{ sortItem.content }}
                              </span>
                              <div class="choiceIcon" v-if="que.showEdArea">
                                <div class="pic-holder">
                                  <input
                                    type="file"
                                    name="file"
                                    class="file canNotSee"
                                    :id="sortItem.fileId"
                                    @change="
                                      (event) =>
                                        picHandler(event, sortItem, 'item')
                                    "
                                  />
                                  <label
                                    :for="sortItem.fileId"
                                    class="label-file label-item"
                                  >
                                    <img
                                      v-if="!sortItem.imgSrc"
                                      src="../assets/pictures/pic.svg"
                                      width="16px"
                                    />
                                    <img
                                      v-else
                                      :src="sortItem.imgSrc"
                                      class="pic-item"
                                    />
                                  </label>
                                  <div class="deletePic" v-if="sortItem.imgSrc">
                                    <i
                                      class="el-icon-close"
                                      @click="delItemImg(sortItem)"
                                    ></i>
                                  </div>
                                </div>
                                <img
                                  src="../assets/pictures/delete.svg"
                                  width="16px"
                                  @click="deleteSI(que.extension, index)"
                                />
                              </div>
                            </li>
                            <div class="pic-preview">
                              <el-image
                                v-if="!que.showEdArea && sortItem.imgSrc != ''"
                                :src="sortItem.imgSrc"
                                :preview-src-list="[sortItem.imgSrc]"
                                fit="cover"
                                style="width: 135px;height: 90px;border-radius: 5px;"
                              ></el-image>
                            </div>
                          </div>
                        </transition-group>
                      </draggable>

                      <div
                        class="edArea"
                        v-if="que.type == '排序题' && que.showEdArea"
                      >
                        <div class="add" @click="addSI(que.extension)">
                          <img src="../assets/pictures/add.svg" width="18px" />
                          <div>添加选项</div>
                        </div>
                        <div class="edhead">题目设置</div>
                        <div class="item">
                          是否必填<input
                            type="checkbox"
                            v-model="que.compulsory"
                            class="checkBox"
                          />
                        </div>
                        <div class="item">
                          随机选项<input
                            type="checkbox"
                            v-model="que.random"
                            class="checkBox"
                          />
                        </div>
                        <div class="item jump">
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.showUpCondition, que.id)"
                              >显示条件</el-button
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                    <div v-else-if="que.type == '评分题'">
                      <div>
                        <div class="slider">
                          <el-slider
                            v-model="que.extension.value"
                            :marks="que.extension.marks"
                            :min="que.extension.min"
                            :max="que.extension.max"
                            :step="
                              que.extension.max - que.extension.min > 10
                                ? 0.1
                                : 0.01
                            "
                            :show-tooltip="false"
                            disabled
                          >
                          </el-slider>
                        </div>
                      </div>
                      <div
                        class="edArea"
                        v-if="que.type == '评分题' && que.showEdArea"
                      >
                        <div class="edhead">题目设置</div>
                        <div class="item">
                          是否必填<input
                            type="checkbox"
                            v-model="que.compulsory"
                            class="checkBox"
                          />
                        </div>
                        <div class="item">
                          分值范围
                          <div class="max-min sliderM">
                            <input
                              type="text"
                              v-model="que.extension.inputMin"
                              @blur="sliderMinCheck(que.extension)"
                              @keyup.enter="sliderMinCheck(que.extension)"
                            />
                          </div>
                          ~
                          <div class="max-min sliderM">
                            <input
                              type="text"
                              v-model="que.extension.inputMax"
                              @blur="sliderMaxCheck(que.extension)"
                              @keyup.enter="sliderMaxCheck(que.extension)"
                            />
                          </div>
                        </div>
                        <div class="item jump">
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.showUpCondition, que.id)"
                              >显示条件</el-button
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                    <div v-else-if="que.type == '矩阵单选题'">
                      <div class="matrix-holder" v-show="!que.showEdArea">
                        <div class="header-preview">
                          <div class="empty"></div>
                          <div class="columnTitles">
                            <div
                              v-for="(column, index) in que.extension
                                .columnVals"
                              :key="index"
                            >
                              <span>{{ column.content }}</span>
                            </div>
                          </div>
                        </div>
                        <div
                          v-for="(row, index) in que.extension.rowVals"
                          :key="index"
                          class="row-item"
                        >
                          <div class="row-item-title">{{ row.content }}</div>
                          <div class="row-item-choices">
                            <div
                              v-for="(column, index) in que.extension
                                .columnVals"
                              :key="index"
                              class="row-item-choice"
                            >
                              <div>
                                <input
                                  type="radio"
                                  v-model="row.value"
                                  :value="column.content"
                                  disabled
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="matrix-holder" v-show="que.showEdArea">
                        <div class="header-editing">
                          <div class="left-align">问题管理</div>
                          <div class="left-align">选项管理</div>
                          <div style="flex:1">添加 / 删除</div>
                        </div>
                        <div class="inputs-holder">
                          <div class="rowTitles">
                            <textarea
                              type="text"
                              class="textArea-row"
                              v-model="que.extension.row_blank_fill"
                              @blur="rowTitlesHandler(que.extension, false)"
                              :placeholder="`行标题一\r\n行标题二`"
                            ></textarea>
                          </div>
                          <div class="matrix-choices">
                            <draggable
                              v-model="que.extension.columnVals"
                              :scroll="true"
                            >
                              <transition-group name="mrcs" tag="div">
                                <div
                                  v-for="(item, index) in que.extension
                                    .columnVals"
                                  :key="item.id"
                                  class="mrcs-item"
                                >
                                  <li>
                                    <input
                                      type="text"
                                      v-model="item.content"
                                      autocomplete="off"
                                      class="mrcInput"
                                      v-if="que.showEdArea"
                                    />
                                    <span v-else>
                                      {{ item.content }}
                                    </span>
                                    <div class="mrcIcon" v-if="que.showEdArea">
                                      <img
                                        src="../assets/pictures/add.svg"
                                        width="18px"
                                        @click="addMCI(que.extension, index)"
                                      />
                                      <img
                                        src="../assets/pictures/delete.svg"
                                        width="16px"
                                        @click="deleteMCI(que.extension, index)"
                                      />
                                    </div>
                                  </li>
                                </div>
                              </transition-group>
                            </draggable>
                          </div>
                        </div>
                      </div>

                      <div
                        class="edArea matrix"
                        v-if="que.type == '矩阵单选题' && que.showEdArea"
                      >
                        <div class="edhead">题目设置</div>
                        <div class="item">
                          是否必填<input
                            type="checkbox"
                            v-model="que.compulsory"
                            class="checkBox"
                          />
                        </div>
                        <div class="item">
                          随机选项<input
                            type="checkbox"
                            v-model="que.random"
                            class="checkBox"
                          />
                        </div>
                        <div class="item jump">
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.showUpCondition, que.id)"
                              >显示条件</el-button
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                    <div v-else-if="que.type == '矩阵多选题'">
                      <div class="matrix-holder" v-show="!que.showEdArea">
                        <div class="header-preview">
                          <div class="empty"></div>
                          <div class="columnTitles">
                            <div
                              v-for="(column, index) in que.extension
                                .columnVals"
                              :key="index"
                            >
                              <span>{{ column.content }}</span>
                            </div>
                          </div>
                        </div>
                        <div
                          v-for="(row, index) in que.extension.rowVals"
                          :key="index"
                          class="row-item"
                        >
                          <div class="row-item-title">{{ row.content }}</div>
                          <div class="row-item-choices">
                            <div
                              v-for="column in que.extension.columnVals"
                              :key="column.id"
                              class="row-item-choice"
                            >
                              <div>
                                <input
                                  type="checkbox"
                                  v-model="row.value"
                                  :value="column.content"
                                  disabled
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="matrix-holder" v-show="que.showEdArea">
                        <div class="header-editing">
                          <div class="left-align">问题管理</div>
                          <div class="left-align">选项管理</div>
                          <div style="flex:1">添加 / 删除</div>
                        </div>
                        <div class="inputs-holder">
                          <div class="rowTitles">
                            <textarea
                              type="text"
                              class="textArea-row"
                              v-model="que.extension.row_blank_fill"
                              @blur="rowTitlesHandler(que.extension, true)"
                              :placeholder="`行标题一\r\n行标题二\r\n行标题三`"
                            ></textarea>
                          </div>
                          <div class="matrix-choices">
                            <draggable
                              v-model="que.extension.columnVals"
                              :scroll="true"
                            >
                              <transition-group name="mmcs" tag="div">
                                <div
                                  v-for="(item, index) in que.extension
                                    .columnVals"
                                  :key="item.id"
                                  class="mmcs-item"
                                >
                                  <li>
                                    <input
                                      type="text"
                                      v-model="item.content"
                                      autocomplete="off"
                                      class="mrcInput"
                                      v-if="que.showEdArea"
                                    />
                                    <span v-else>
                                      {{ item.content }}
                                    </span>
                                    <div class="mrcIcon" v-if="que.showEdArea">
                                      <img
                                        src="../assets/pictures/add.svg"
                                        width="18px"
                                        @click="addMCI(que.extension, index)"
                                      />
                                      <img
                                        src="../assets/pictures/delete.svg"
                                        width="16px"
                                        @click="deleteMCI(que.extension, index)"
                                      />
                                    </div>
                                  </li>
                                </div>
                              </transition-group>
                            </draggable>
                          </div>
                        </div>
                      </div>
                      <div
                        class="edArea matrix"
                        v-if="que.type == '矩阵多选题' && que.showEdArea"
                      >
                        <div class="edhead">题目设置</div>
                        <div class="item">
                          是否必填<input
                            type="checkbox"
                            v-model="que.compulsory"
                            class="checkBox"
                          />
                        </div>
                        <div class="item">
                          随机选项<input
                            type="checkbox"
                            v-model="que.random"
                            class="checkBox"
                          />
                        </div>
                        <div class="item">
                          每行选择数
                          <div class="max-min">
                            至多<input
                              type="text"
                              v-model="que.extension.max"
                              @blur="multiMaxCheck(que.extension, true)"
                              @keyup.enter="multiMaxCheck(que.extension, true)"
                            />
                          </div>
                          <div class="max-min">
                            至少<input
                              type="text"
                              v-model="que.extension.min"
                              @blur="multiMinCheck(que.extension, true)"
                              @keyup.enter="multiMinCheck(que.extension, true)"
                            />
                          </div>
                        </div>
                        <div class="item jump">
                          <div class="text">
                            <el-button
                              type="text"
                              @click="openDialog(que.showUpCondition, que.id)"
                              >显示条件</el-button
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                    <div v-else class="divisionLine">
                      <div v-show="!que.showEdArea">
                        [ 第{{ pagingLineIndex(que.id) + 1 }}页 / 共{{
                          totalPage
                        }}页 ]
                      </div>
                      <div v-show="que.showEdArea">
                        <img
                          src="../assets/pictures/delete.svg"
                          width="16px"
                          @click="deletepagingLine(index)"
                        />
                      </div>
                    </div>

                    <div
                      class="buttons"
                      v-if="que.showEdArea && que.type != 'pagingLine'"
                    >
                      <div v-for="button in buttons" :key="button.type">
                        <button
                          type="button"
                          @click="Fn(button.func, que, index)"
                        >
                          <img
                            :src="button.imgSrc"
                            width="12px"
                            height="12px"
                          />
                          <div>{{ button.type }}</div>
                        </button>
                      </div>
                    </div>
                  </div>
                </transition-group>
              </draggable>
            </div>
          </div>
        </div>
        <div id="generalButts">
          <div>
            <img
              src="../assets/pictures/settings.svg"
              width="25px"
              height="25px"
            /><span>设置</span>
          </div>
          <div>
            <img
              src="../assets/pictures/save.svg"
              width="23px"
              height="23px"
            /><span>保存</span>
          </div>
          <div>
            <img
              src="../assets/pictures/publish.svg"
              width="21px"
              height="21px"
            /><span>发布</span>
          </div>
          <div @click="toPreview">
            <img
              src="../assets/pictures/preview.svg"
              width="19px"
              height="19px"
            /><span>预览</span>
          </div>
          <div @click="toHome">
            <img
              src="../assets/pictures/back.svg"
              width="18px"
              height="18px"
            /><span>返回</span>
          </div>
        </div>
      </div>
      <el-dialog
        title=""
        :visible.sync="dialogFormVisible"
        destroy-on-close
        @open="removeListener"
        @closed="callListener"
      >
        <div class="dialogHolder" v-if="isBfa">
          <div class="bfaTitle">设置选项后填空</div>
          <div class="bfaIntp">
            可在选项后设置填空框，供填写者填写更多信息。
          </div>
          <div class="bfaLine"></div>
          <div class="header-bfa">
            <div style="flex:3;text-align:left;margin:auto -15px auto 15px;">
              选项名称
            </div>
            <div style="flex:1;text-align:center;">设置输入框</div>
            <div style="flex:1;text-align:center;">必填</div>
          </div>
          <div v-for="data in dialogTempData" :key="data.id" class="item-bfa">
            <div class="item-bfa-content">{{ data.content }}</div>
            <div class="item-bfa-inputBox">
              <input
                type="checkbox"
                v-model="data.needFilling"
                @change="coupling(data)"
              />
            </div>
            <div class="item-bfa-inputBox">
              <input
                type="checkbox"
                v-model="data.compulsory"
                :disabled="!data.needFilling"
              />
            </div>
          </div>
        </div>
        <div class="dialogHolder" v-else>
          <div class="bfaTitle">设置显示条件</div>
          <div class="bfaIntp">
            当填写者的选择满足以下任意一个条件时，则显示本题。
          </div>
          <div class="bfaLine"></div>
          <div
            v-for="(condition, index) in dialogTempData"
            :key="index"
            class="conditionHolder"
          >
            <div class="selectHolder">
              <el-select
                v-model="condition.titleVal"
                placeholder="请选择"
                filterable
                size="small"
                @change="condition.itemsVal = []"
              >
                <el-option
                  v-for="que in radios"
                  :key="que.id"
                  :label="queUltimateIndex(que.id) + 1 + '.' + que.title"
                  :value="que.id"
                  :disabled="rationalityCheck(que.id, condition.titleVal)"
                >
                </el-option>
              </el-select>
              选中
            </div>
            <div class="selectHolder">
              <el-select
                v-model="condition.itemsVal"
                multiple
                filterable
                size="small"
                :disabled="condition.titleVal === ''"
                placeholder="请选择"
              >
                <el-option
                  v-for="choice in !condition.titleVal
                    ? []
                    : ques[queIndex(condition.titleVal)].extension.items"
                  :key="choice.id"
                  :label="choice.content"
                  :value="choice.id"
                >
                </el-option>
              </el-select>
              <img
                src="../assets/pictures/delete.svg"
                width="16px"
                @click="deleteCondition(index)"
              />
            </div>
          </div>
          <div class="add" @click="addCondition">
            <img src="../assets/pictures/add.svg" width="18px" />
            <div>添加条件</div>
          </div>
        </div>
        <el-button
          type="primary"
          @click="dialogFormVisible = false"
          class="dialogButton"
          >确 定</el-button
        >
      </el-dialog>
    </div>
  </div>
</template>

<script>
import queHeader from "@/components/HeaderComponent.vue";
import draggable from "vuedraggable";
import { mapState } from "vuex";
export default {
  name: "EditPage",
  components: {
    queHeader,
    draggable,
  },
  data() {
    return {
      types: [
        "单选题",
        "多选题",
        "填空题",
        "排序题",
        "评分题",
        "矩阵单选题",
        "矩阵多选题",
      ],
      radio: {
        value: "",
        idForNext: 4,
        items: [
          {
            id: 1,
            content: "选项1",
            fileId: "cs1",
            imgSrc: "",
            needFilling: false,
            compulsory: false,
            blank_fill: "",
          },
          {
            id: 2,
            content: "选项2",
            fileId: "cs2",
            imgSrc: "",
            needFilling: false,
            compulsory: false,
            blank_fill: "",
          },
          {
            id: 3,
            content: "选项3",
            fileId: "cs3",
            imgSrc: "",
            needFilling: false,
            compulsory: false,
            blank_fill: "",
          },
        ],
      },
      multiple: {
        value: [],
        idForNext: 3,
        max: "不限",
        min: "0",
        items: [
          {
            id: 1,
            content: "选项1",
            fileId: "op1",
            imgSrc: "",
            needFilling: false,
            compulsory: false,
            blank_fill: "",
          },
          {
            id: 2,
            content: "选项2",
            fileId: "op2",
            imgSrc: "",
            needFilling: false,
            compulsory: false,
            blank_fill: "",
          },
        ],
      },
      blank_fill: "",
      sort: {
        idForSI: 4,
        sortedNum: 0,
        items: [
          {
            id: 1,
            content: "选项1",
            fileId: "si1",
            imgSrc: "",
            order: 0,
          },
          {
            id: 2,
            content: "选项2",
            fileId: "si2",
            imgSrc: "",
            order: 0,
          },
          {
            id: 3,
            content: "选项3",
            fileId: "si3",
            imgSrc: "",
            order: 0,
          },
        ],
      },
      slider: {
        value: [0, 100],
        max: 100,
        min: 0,
        inputMax: "100",
        inputMin: "0",
        showToolTip: false,
        marks: {
          0: "0",
          20: "20",
          40: "40",
          60: "60",
          80: "80",
          100: {
            style: {
              // color: "#1989FA",
            },
            label: "100",
          },
        },
      },
      matrixRadio: {
        row_blank_fill: "",
        idForNext: 4,
        rowVals: [
          { content: "行标题1", value: "" },
          { content: "行标题2", value: "" },
        ],
        columnVals: [
          { content: "选项1", id: 1 },
          { content: "选项2", id: 2 },
          { content: "选项3", id: 3 },
        ],
      },
      matrixMultiple: {
        row_blank_fill: "",
        idForNext: 4,
        max: "不限",
        min: "0",
        rowVals: [
          { content: "行标题1", value: [] },
          { content: "行标题2", value: [] },
          { content: "行标题3", value: [] },
        ],
        columnVals: [
          { content: "选项1", id: 1 },
          { content: "选项2", id: 2 },
          { content: "选项3", id: 3 },
        ],
      },
      buttons: [
        {
          type: "复制",
          func: "cloneQue",
          imgSrc: require("../assets/pictures/copy.svg"),
        },
        {
          type: "删除",
          func: "deleteQue",
          imgSrc: require("../assets/pictures/delete.svg"),
        },
        {
          type: "分页",
          func: "paging",
          imgSrc: require("../assets/pictures/paging.svg"),
        },
      ],
      onload: false,
      loading: true,
      queName: "问卷名称",
      isEdName: false, //'ed' => 'editing'
      queIntp: "问卷说明",
      isEdIntp: false,
      dialogFormVisible: false,
      isBfa: false,
      dialogTempData: [],
      dialogTempId: 0,
      currentPage: 1,
      totalPage: 1,
      idForQues: 1,
      ques: [],
    };
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      },
    },
  },
  mounted() {
    if (Object.keys(this.questionnaireObj).length) {
      this.queName = this.questionnaireObj.name;
      this.queIntp = this.questionnaireObj.interpretation;
      this.totalPage = this.questionnaireObj.totalPage;
      this.idForQues = this.questionnaireObj.idForQues;
      this.ques = JSON.parse(JSON.stringify(this.questionnaireObj.data));
    }
    window.addEventListener("click", this.clickHandler);
    this.onload = true;
    this.loading = false;
  },
  computed: {
    ...mapState(["questionnaireObj"]),
    quesUltimate() {
      return this.ques.filter((que) => que.type != "pagingLine");
    },
    pagingLines() {
      return this.ques.filter((que) => que.type == "pagingLine");
    },
    showTip() {
      var flag = 0;
      this.ques.map((que) => {
        if (que.page === this.currentPage) flag++;
      });
      return flag === 0;
    },
    radios() {
      return this.ques.filter((que) => que.type == "单选题");
    },
  },
  methods: {
    quitEdName() {
      this.isEdName = false;
      if (this.queName.trim().length == 0) this.queName = "问卷名称";
    },
    quitEdIntp() {
      this.isEdIntp = false;
      if (this.queIntp.trim().length == 0) this.queIntp = "问卷说明";
    },
    cQ(param) {
      var extension =
        param === "单选题"
          ? this.deep(this.radio)
          : param === "多选题"
          ? this.deep(this.multiple)
          : param === "填空题"
          ? this.deep(this.blank_fill)
          : param === "排序题"
          ? this.deep(this.sort)
          : param === "评分题"
          ? this.deep(this.slider)
          : param === "矩阵单选题"
          ? this.deep(this.matrixRadio)
          : this.deep(this.matrixMultiple);
      this.ques.push({
        id: this.idForQues,
        title: "这是一道" + param,
        isEdTitle: false,
        titleImgSrc: "",
        type: param,
        page: this.currentPage,
        showEdArea: false,
        compulsory: true,
        random: false,
        showUpCondition: [{ titleVal: "", itemsVal: [] }],
        extension,
      });
      this.idForQues++;
    },
    editQue(que, e) {
      if (que.showEdArea || e.target.nodeName == "IMG") return;
      this.ques.forEach((que) => { que.showEdArea = false });
      que.showEdArea = true;
    },
    deleteQue(que) {
      if (que.type === "单选题")
        this.ques.map((unit) => {
          if (unit.type != "pagingLine")
            for (var i = unit.showUpCondition.length - 1; i >= 0; i--) {
              if (unit.showUpCondition[i].titleVal == que.id)
                unit.showUpCondition.splice(i, 1);
              if (unit.showUpCondition == false)
                unit.showUpCondition.push({ titleVal: "", itemsVal: [] });
            }
        });
      let delIndex = this.ques.indexOf(que);
      this.ques.splice(delIndex, 1);
    },
    cloneQue(que) {
      var cloneIndex = this.ques.indexOf(que);
      var deepClone_obj = JSON.parse(JSON.stringify(this.ques[cloneIndex]));
      deepClone_obj.id = this.idForQues++;
      deepClone_obj.showEdArea = false;
      this.ques.splice(cloneIndex + 1, 0, deepClone_obj);
    },
    // up(que, index) {
    //   if (index == 0) {
    //     if (que.page == 1) {
    //       this.$message({
    //         message: "第一页第一题不能再上移",
    //         type: "warning",
    //       });
    //       return;
    //     } else que.page--;
    //   } else {
    //     var i = que.id - 1;
    //     this.ques[i - 1] = this.ques.splice(i, 1, this.ques[i - 1])[0];
    //     que.id--;
    //     this.ques[i].id++;
    //   }
    // },
    // down(que, index) {
    //   let k = 0;
    //   this.ques.map((que) => {
    //     if (que.page == this.currentPage) k++;
    //   });
    //   if (index == k - 1) {
    //     if (que.page == this.totalPage) {
    //       this.$message({
    //         message: "最后一页最后一题不能再下移",
    //         type: "warning",
    //       });
    //       return;
    //     } else que.page++;
    //   } else {
    //     var i = que.id - 1;
    //     this.ques[i + 1] = this.ques.splice(i, 1, this.ques[i + 1])[0];
    //     que.id++;
    //     this.ques[i].id--;
    //   }
    // },
    picHandler(event, obj, type) {
      var file = event.target.files[0];
      if (!file) return;
      else if (
        file.type !== "image/jpeg" &&
        file.type !== "image/png" &&
        file.type !== "image/gif"
      ) {
        this.$message.warning("不是有效的图片文件!");
        return;
      }
      var reader = new FileReader();
      reader.onload = function(e) {
        var data = e.target.result;
        //赋值src
        if (type === "title") obj.titleImgSrc = data;
        else obj.imgSrc = data;
      };
      reader.readAsDataURL(file);
    },
    delTitleImg(que) {
      setTimeout(() => {
        que.titleImgSrc = "";
      }, 50);
    },
    delItemImg(item) {
      setTimeout(() => {
        item.imgSrc = "";
      }, 50);
    },
    //functions-radio
    addCS(radio) {
      radio.items.push({
        content: `选项${radio.items.length + 1}`,
        id: radio.idForNext,
        fileId: `cs${radio.idForNext}`,
        imgSrc: "",
        needFilling: false,
        compulsory: false,
        blank_fill: "",
      });
      radio.idForNext++;
    },
    deleteCS(items, index, uniqueId) {
      if (items.length == 2) {
        this.$message("单选题至少两个选项");
        return;
      }
      this.ques.map((unit) => {
        if (unit.type != "pagingLine")
          unit.showUpCondition.map((condition) => {
            if (condition.titleVal == uniqueId)
              if (!(condition.itemsVal == false))
                for (var j = condition.itemsVal.length - 1; j >= 0; j--)
                  if (condition.itemsVal[j] == items[index].id)
                    condition.itemsVal.splice(j, 1);
          });
      });
      setTimeout(() => {
        items.splice(index, 1);
        for (let i = 0; i < items.length; i++)
          if (/^\+?[1-9][0-9]*$/.test(items[i].content.split("选项")[1]))
            items[i].content = `选项${i + 1}`;
      }, 50);
    },
    //functions-multiple
    addOM(multiple) {
      multiple.items.push({
        content: `选项${multiple.items.length + 1}`,
        id: multiple.idForNext,
        fileId: `op${multiple.idForNext}`,
        imgSrc: "",
        needFilling: false,
        compulsory: false,
        blank_fill: "",
      });
      multiple.idForNext++;
    },
    deleteOM(items, index) {
      if (items.length == 2) {
        this.$message("多选题至少两个选项");
        return;
      }
      setTimeout(() => {
        items.splice(index, 1);
        for (let i = 0; i < items.length; i++) {
          if (/^\+?[1-9][0-9]*$/.test(items[i].content.split("选项")[1]))
            items[i].content = `选项${i + 1}`;
        }
      }, 50);
    },
    multiMaxCheck(multiple, bool) {
      let inputValue = multiple.max;
      if (inputValue == "不限") return;
      var valid = /^\+?[1-9][0-9]*$/.test(inputValue); //正整数
      if (valid) {
        var num = parseInt(inputValue);
        var total = !bool ? multiple.items.length : multiple.columnVals.length; //多选还是矩阵多选
        if (num < 2 || num < multiple.min || num > total) {
          multiple.max = "不限";
          this.$message.warning("最多勾选数应大于2、大于最少且不大于选项总数");
        } else return;
      } else {
        multiple.max = "不限";
        this.$message.warning("请输入一个正整数");
      }
    },
    multiMinCheck(multiple, bool) {
      let inputValue = multiple.min;
      var maximum = multiple.max;
      var valid = /^\d+$/.test(inputValue); //非负整数
      if (valid) {
        var num = parseInt(inputValue);
        var total = !bool ? multiple.items.length : multiple.columnVals.length;
        if (maximum == "不限") {
          if (num > total) {
            multiple.min = "0";
            this.$message.warning("最少勾选数不应大于选项总数");
          }
        } else if (num > multiple.max) {
          multiple.min = "0";
          this.$message.warning("最少勾选数不应大于最多");
        } else return;
      } else {
        multiple.min = "0";
        this.$message.warning("请输入一个正整数");
      }
    },
    addSI(sort) {
      sort.items.push({
        content: `选项${sort.items.length + 1}`,
        id: sort.idForSI,
        fileId: `cs${sort.idForSI}`,
        imgSrc: "",
        order: 0,
      });
      sort.idForSI++;
    },
    deleteSI(sort, index) {
      if (sort.items.length == 3) {
        this.$message("排序题至少三个选项");
        return;
      }
      setTimeout(() => {
        sort.items.splice(index, 1);
        for (let i = 0; i < sort.items.length; i++) {
          if (/^\+?[1-9][0-9]*$/.test(sort.items[i].content.split("选项")[1]))
            sort.items[i].content = `选项${i + 1}`;
        }
      }, 50);
    },
    sliderMinCheck(slider) {
      let min = parseFloat(slider.inputMin);
      let max = slider.max;
      var valid = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/.test(min); //非负数
      if (valid) {
        if (min < max && max - min >= 0.5) {
          let bigRange = max - min > 10;
          min = bigRange ? this.oneDecimal(min) : this.twoDecimal(min);
          slider.inputMin = `${min}`;
          slider.min = min;
          this.divideSlider(slider, min, max, bigRange);
        } else {
          slider.inputMin = `${slider.min}`;
          this.$message.warning("最小值须小于最大值，最小分差0.5");
        }
      } else {
        slider.inputMin = `${slider.min}`;
        this.$message.warning("请输入一个非负分值");
      }
    },
    sliderMaxCheck(slider) {
      let min = slider.min;
      let max = parseFloat(slider.inputMax);
      var valid = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/.test(max); //非负数
      if (valid) {
        if (min < max && max - min >= 0.5) {
          let bigRange = max - min > 10;
          max = bigRange ? this.oneDecimal(max) : this.twoDecimal(max);
          slider.inputMax = `${max}`;
          slider.max = max;
          this.divideSlider(slider, min, max, bigRange);
        } else {
          slider.inputMax = `${slider.max}`;
          this.$message.warning("最大值须大于最小值，最小分差0.5");
        }
      } else {
        slider.inputMax = `${slider.max}`;
        this.$message.warning("请输入一个非负分值");
      }
    },
    divideSlider(slider, min, max, bool) {
      var zero = min;
      var oneFifth = bool
        ? this.oneDecimal(min + (max - min) / 5)
        : this.twoDecimal(min + (max - min) / 5);
      var twoFifths = bool
        ? this.oneDecimal(min + ((max - min) * 2) / 5)
        : this.twoDecimal(min + ((max - min) * 2) / 5);
      var threeFifths = bool
        ? this.oneDecimal(min + ((max - min) * 3) / 5)
        : this.twoDecimal(min + ((max - min) * 3) / 5);
      var fourFifths = bool
        ? this.oneDecimal(min + ((max - min) * 4) / 5)
        : this.twoDecimal(min + ((max - min) * 4) / 5);
      var whole = max;
      slider.marks = {
        [zero]: `${zero}`,
        [oneFifth]: `${oneFifth}`,
        [twoFifths]: `${twoFifths}`,
        [threeFifths]: `${threeFifths}`,
        [fourFifths]: `${fourFifths}`,
        [whole]: {
          style: {
            // color: "#1989FA",
          },
          label: `${whole}`,
        },
      };
      slider.value = min;
    },
    oneDecimal(num) {
      return Math.round(num * 10) / 10;
    },
    twoDecimal(num) {
      return Math.round(num * 100) / 100;
    },
    addMCI(matrix, index) {
      //matrix-column-item
      matrix.columnVals.splice(index + 1, 0, {
        content: `选项${matrix.columnVals.length + 1}`,
        id: matrix.idForNext,
      });
      matrix.idForNext++;
    },
    deleteMCI(matrix, index) {
      if (matrix.columnVals.length == 2) {
        this.$message("对每个行标题至少两个选项");
        return;
      }
      setTimeout(() => {
        matrix.columnVals.splice(index, 1);
        for (let i = 0; i < matrix.columnVals.length; i++)
          if (
            /^\+?[1-9][0-9]*$/.test(
              matrix.columnVals[i].content.split("选项")[1]
            )
          )
            matrix.columnVals[i].content = `选项${i + 1}`;
      }, 50);
    },
    rowTitlesHandler(matrix, bool) {
      var rowValues = matrix.row_blank_fill.split(/[(\r\n)\r\n]+/);
      rowValues.forEach((item, index) => {
        if (!item) rowValues.splice(index, 1);
      });
      if (rowValues.length && rowValues != "") {
        matrix.rowVals = [];
        rowValues.map((item) =>
          matrix.rowVals.push({
            content: item,
            value: bool ? [] : "",
          })
        );
      } else
        this.$message.warning(
          "不是有效的行标题。提示：行标题间请输入回车键区分。"
        );
    },
    //
    conduct(method, item) {
      this[method](item);
    },
    Fn(method, que, index) {
      this[method](que, index);
    },
    paging(que, index) {
      this.ques.splice(index + 1, 0, {
        id: this.idForQues,
        type: "pagingLine",
        page: this.currentPage,
        showEdArea: false,
      });
      this.idForQues++;
      this.totalPage++;
      this.$message("您为问卷新建了一页");
    },
    deletepagingLine(index) {
      this.ques.splice(index, 1);
      this.totalPage--;
    },
    //
    sortById(array, key) {
      return array.sort(function(a, b) {
        var xx = a[key];
        var xy = b[key];
        return xx < xy ? -1 : xx > xy ? 1 : 0;
      });
    },
    //
    openDialog(data, info) {
      this.dialogTempData = data;
      if (info === "bfa") this.isBfa = true;
      else this.dialogTempId = info;
      this.dialogFormVisible = true;
    },
    coupling(data) {
      if (!data.needFilling && data.compulsory) data.compulsory = false;
    },
    queIndex(id) {
      let i = -1;
      this.ques.map((que, index) => {
        if (que.id === id) i = index;
      });
      return i;
    },
    queUltimateIndex(id) {
      let i = -1;
      this.quesUltimate.map((que, index) => {
        if (que.id === id) i = index;
      });
      return i;
    },
    pagingLineIndex(id) {
      let i = -1;
      this.pagingLines.map((que, index) => {
        if (que.id === id) i = index;
      });
      return i;
    },
    addCondition() {
      this.dialogTempData.push({
        titleVal: "",
        itemsVal: [],
      });
    },
    deleteCondition(index) {
      this.dialogTempData.splice(index, 1);
      if (this.dialogTempData == false) {
        this.$message.warning("保留至少一个以编辑");
        this.dialogTempData.push({
          titleVal: "",
          itemsVal: [],
        });
      }
    },
    rationalityCheck(id, self) {
      let flag = false;
      this.dialogTempData.map((condition) => {
        if (condition.titleVal == id)
          if (condition.titleVal != self) flag = true;
      });
      if (this.queIndex(id) >= this.queIndex(this.dialogTempId)) flag = true;
      return flag;
    },
    itemSwitched() {
      let flag = false;
      this.quesUltimate.map((que, index) => {
        que.showUpCondition.map((condition, i) => {
          if (this.queUltimateIndex(condition.titleVal) > index) {
            flag = true;
            que.showUpCondition.splice(i, 1);
            if (que.showUpCondition == false)
              que.showUpCondition.push({ titleVal: "", itemsVal: [] });
          }
        });
      });
      if (flag)
        this.$message.warning(
          "您对于题目顺序的改变导致了某些‘显示条件’不合理，不合理的显示条件已删除。"
        );
    },
    deep(obj) {
      return JSON.parse(JSON.stringify(obj));
    },
    clickHandler(e) {
      let node = e.target;
      if (node.nodeName == "IMG") return;
      if (node.nodeName == "SPAN") node = e.path[1];
      if (!document.getElementById("models").contains(node))
        this.ques.map((que) => (que.showEdArea = false));
    },
    removeListener() {
      window.removeEventListener("click", this.clickHandler);
    },
    callListener() {
      if (!(this.dialogTempData == false))
        //判断数组非空
        this.dialogTempData.map((data) => {
          if (data.titleVal != "" && data.itemsVal == false) data.titleVal = "";
        });
      window.addEventListener("click", this.clickHandler);
      this.isBfa = false;
    },
    toPreview() {
      var self = this;
      function emitQues(success) {
        return new Promise((resolve, reject) => {
          console.log("准备中...");
          if (success) {
            self.$store.commit("emitQues", {
              name: self.queName,
              interpretation: self.queIntp,
              idForQues: self.idForQues,
              totalPage: self.totalPage,
              data: self.ques,
            });
            resolve();
          } else reject();
        });
      }
      (async () => {
        try {
          await emitQues(this.quesUltimate.length);
          console.log("emit succeed!");
          this.$router.push({ name: "Preview" });
        } catch {
          console.log("emit failed!");
          this.$message.warning("您还未添加题目");
        }
      })();
    },
    toHome() {
      this.$router.push({ name: "HomePage" });
    },
  },
  destroyed() {
    window.removeEventListener("click", this.clickHandler);
  },
};
</script>

